header {
  width: 100vw;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
}
.logo {
  width: 155px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.navegator {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  padding-bottom: 10px;
  text-transform: uppercase;
  font-size: 16px;
}
.blog,
.contacto {
  padding: 10px;
  text-decoration: none;
  color: #4a4a4a;
}
